import React from 'react';

class Header extends React.Component {
  render() {
    return (
      <div className="header">
        <img className="header-logo" src="./logo.png" height="80"/>
        <div className="header-menu">
          <a href="https://github.com/SamYStudiO/es-theme-next-pixel/blob/master/README.md" className="header-menuItem">
            About
          </a>
          <a href="https://github.com/SamYStudiO/es-theme-next-pixel/releases" className="header-menuItem">
            Download
          </a>
          <a href="https://github.com/SamYStudiO/es-theme-next-pixel" className="header-menuItem">
            Project
          </a>
        </div>
        <div className="header-recalbox-container">
          <a className="header-recalbox" href="https://www.recalbox.com/">
            FOR RECALBOX 6.1+
          </a>
          <img className="header-recalbox-logo" src="./recalbox_logo.png" width="40"/>
        </div>
      </div>
    );
  }
}

export default Header;
